@import "variaveis";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-1;
  color: $font-color;
}
html {
  scroll-behavior: smooth;
}
body {
  @include background;
}

main {
  margin-top: 70px;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.container {
  width: 1250px;
  height: auto;

  display: flex;
  justify-content: space-between;
}

section.header {
  width: 310px;
  height: 800px;
  padding: 20px 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  position: fixed;

  background: $backg-section;

  -webkit-box-shadow: 0px 18px 20px -6px rgba(0, 0, 0, 0.849);
  box-shadow: 0px 18px 20px -6px rgba(0, 0, 0, 0.76);
}

.header .profile-pic img {
  width: 270px;
}

.header .title-profile {
  width: 270px;
  height: 30px;
  text-align: center;

  h1 {
    font-family: $font-2;
    font-weight: bold;
    font-size: 22px;
    color: $font-color;
  }
}

nav {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .menu {
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    li {
      width: 270px;
      cursor: pointer;
      list-style-type: none;
      display: block;
      margin: 3px 0;
      border-radius: none;
      a {
        color: #00a2ff;
        text-align: center;
        display: block;
        padding: 15px 0px;
        text-decoration: none;
        &:hover {
          background-color: #00000054;
          color: $font-color;
          transition: .3s;
        }
      }

      @keyframes lineMove {
        0% {
          background-position: 0 100%;
        }
        100% {
          background-position: 0 10%;
        }
      }

    }
  }
}

.header footer h3 {
  margin-top: 20%;
  font-size: 14px;
  font-weight: 300;
  font-family: $font-3;
}


.content-column {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  height: auto;
}
.content-column section {
  margin: 0 0 63px 0;
}

section.content {
  margin-left: 380px;
  width: calc(100% - 350px);
  height: auto;
  padding: 30px 10px;

  background: $backg-section;
  border-radius: none;
  border-bottom: 01px solid $neon-color;

  -webkit-box-shadow: -6px 9px 17px 2px rgba(0, 0, 0, 0.3);
  box-shadow: -6px 9px 17px 2px rgba(0, 0, 0, 0.3);
  z-index: -100;

  &:nth-child(2) {
    height: auto;
  }
}

div.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 120px 0;

  height: 90%;
  h1 {
    font-size: 32px;

    span {
      color: #fff;
      animation: neonText 3s infinite alternate;
      z-index: 1000;
    }
  }
}
@keyframes neonText {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: $neon-effect;
  }
  20%,
  24%,
  55% {
    text-shadow: none;
  }
}
/*   FORMATAÇÃO CHAMADA   */
div.chamada {
  font-family: $font-2;
  font-weight: 900;
  font-size: $size-font-call;
  text-align: center;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;
  padding: 05px 20px;
  background: lighten(#0b0f15, 10%);
  overflow: hidden;
  border-radius: 10px;

  &::after {
    content: "";
    position: absolute;
    inset: 4px;
    border-radius: 10px;
    background: linear-gradient(145deg, #0b0f15, #090d12);
  }

  h1 {
    position: relative;
    z-index: 10;
    color: lighten(#0b0f15, 10%);
  }
}

/*    FORMATAÇÃO SKILL      */

div.skill {
  width: 100%;
  height: calc(100% - 70px);
  padding: 20px;

  .content-skill {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    gap: 30px;
  }
}

.content-skill .skill-box {

  width: 250px;
  height: 50px;
  border-radius: 05px;
  background: linear-gradient(145deg, #10161f, #0d151b);
  box-shadow: 5px 5px 10px -5px rgb(0, 0, 0);
  h1 {
    padding: 5px 10px;
  }

  img {
    width: 30px;
    float: right;
    position: relative;
    top: -25px;
    left: -10px;
  }
}

.fixedbar {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.438);
  height: 05px;
  border-radius: 05px;

  position: relative;
  top: 11px;
}

.bar {
  height: 05px;
  background: $neon-color;
  border-radius: 05px;
  box-shadow: $neon-effect2;
  animation: slide 3.5s ease-out;

  &::after {
    content: attr(progress) "%";
    color: $neon-color;
    font-weight: bold;
    font-size: 12px;
    float: right;
    display: block;
    position: relative;
    top: 7px;
    left: 0px;
    animation: opacitySlide 3.7s ease;
    text-shadow: 2px 2px 1px black;
  }
}

@keyframes opacitySlide {
  0%,
  98% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slide {
  to {
    width: progress;
  }
  from {
    width: 0%;
  }
}
