$font-1: 'Fira Code', monospace;
$font-2: 'Helvetica', sans-serif;
$font-3: 'IBM Plex Mono', monospace;
$call-font: 'Kanit', sans-serif;
$size-font-call: 24px;

$font-color: #e4eee5;
$backg-color: #13212c;
$backg-section: url(../img/aaaasss.png);
$backg-menuHover: #1f1f1f;

$neon-color: #0084ff;
$neon-effect: 0 0 4px $neon-color,
0 0 11px $neon-color,
0 0 19px $neon-color,
0 0 40px $neon-color,
0 0 80px $neon-color,
0 0 90px $neon-color,
0 0 100px $neon-color,
0 0 150px $neon-color;

$neon-effect2: 0 0 02px $neon-color,
0 0 04px $neon-color,
0 0 06px $neon-color,
0 0 10px $neon-color,
0 0 20px $neon-color;


@mixin neonTextStyle {
  text-shadow:
  0 0 2px #b1efff,
  0 0 4px #b1efff,
  0 0 6px #b1efff,
  0 0 8px $neon-color,
  0 0 10px $neon-color,
  0 0 15px $neon-color,
  0 0 20px $neon-color,
  0 0 40px $neon-color;
}

@mixin background {
  background: url(../img/background.png)
}


